.stars {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;

  &__star {
    background: url(../../images/star.svg) no-repeat center;
    height: 16px;
    width: 16px;
    margin-right: 4px;
  }
}

.stars--4 .stars__star:nth-child(-n+4) {
  background: url(../../images/star-active.svg) no-repeat center;
  margin-right: 4px;
}
